:root {
  --pastel-cream: #fff8e7;
  --pastel-yellow: #fff1a8;
  --pastel-pink: #ffd1e6;
  --soft-text: #3b2a2a;
}
:root {
  --soft-pink: #ffd1e6;       /* main pastel pink */
  --pink-center: #ffb3d1;     /* slightly warmer near candle */
  --pink-edge: #e68ab3;       /* darker pink at edges */
  --soft-text: #3b2a2a;
}


* { box-sizing: border-box; }

.page {
  min-height: 100vh;
  display: flex;
  align-items: stretch;
  justify-content: center;
  background: linear-gradient(180deg, #fffaf6 0%, #fff8f9 100%);
  position: relative;
  overflow: hidden;
  font-family: "Poppins", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial;
  color: var(--soft-text);
  transition: background 800ms ease;
}
.page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  /* radial gradient centered around candle flame */
  background: radial-gradient(
    circle at 50% 35%,
    var(--pink-center) 0%,     /* bright area around flame */
    var(--soft-pink) 40%,      /* soft pastel pink surrounding */
    var(--pink-edge) 100%      /* darker pink edges */
  );
  position: relative;
  overflow-x: auto;
  font-family: "Poppins", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial;
  color: var(--soft-text);
  transition: background 800ms ease;
}
/* Hide scrollbar for WebKit browsers (Chrome, Safari) */
.page::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

/* Hide scrollbar for Firefox */
.page {
  scrollbar-width: none; /* Firefox */
}

.bloom {
  position: absolute;
  left: 50%;
  top: 48%;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  transform: translate(-50%, -50%) scale(0);
  background: radial-gradient(circle at center, var(--pastel-yellow) 0%, rgba(255,241,168,0.95) 40%, rgba(255,217,230,0.5) 100%);
  z-index: 5;
  pointer-events: none;
}

.bloom-animate {
  animation: expandBloom 650ms cubic-bezier(.2,.9,.4,1) forwards, bloomToPink 600ms 560ms forwards;
}

@keyframes expandBloom {
  from { transform: translate(-50%, -50%) scale(0); opacity: 1; }
  to { transform: translate(-50%, -50%) scale(120); opacity: 1; }
}

@keyframes bloomToPink {
  from { background: radial-gradient(circle at center, var(--pastel-yellow) 0%, rgba(255,241,168,0.95) 40%, rgba(255,217,230,0.5) 100%); }
  to { background: radial-gradient(circle at center, var(--pastel-pink) 0%, rgba(255,210,230,0.95) 40%, rgba(255,210,230,0.95) 100%); }
}

.content {
  z-index: 10;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
}

.candle-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 180px;
  height: 340px;
}

.candle-svg {
  position: absolute;
  bottom: 20px;
  filter: drop-shadow(0 6px 20px rgba(0,0,0,0.12));
}

.wick {
  position: absolute;
  width: 6px;
  height: 14px;
  background: #382222;
  border-radius: 2px;
  top: 86px;
  z-index: 8;
}

.flame {
  position: absolute;
  top: 15px;
  width: 50px;
  height: 90px;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  z-index: 12;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
}

.flame:focus {
  box-shadow: 0 0 0 6px rgba(255, 182, 193, 0.18);
  border-radius: 50%;
}

.flame-inner {
  position: absolute;
  width: 50px;
  height: 80px;
  top: -8px;
  left: 50%;
  transform: translateX(-50%);
  background: radial-gradient(
    ellipse at bottom,
    #ffef80 0%,
    #ffc93c 50%,
    #ff8b00 85%,
    rgba(255, 100, 0, 0.9) 100%
  );
  border-radius: 50% 50% 40% 40%;
  box-shadow: 0 0 36px 14px rgba(255, 190, 60, 0.6);
  animation: flickerOuter 0.4s infinite ease-in-out;
}

.flame-core {
  position: absolute;
  width: 25px;
  height: 45px;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  background: radial-gradient(circle at center, #ffb300 0%, #ff8000 60%, #ff4d00 100%);
  border-radius: 50% 50% 40% 40%;
  box-shadow: 0 0 16px rgba(255,120,20,0.8);
  animation: flickerCore 0.3s infinite ease-in-out;
}

@keyframes flickerOuter {
  0% { transform: translateX(-50%) scaleY(1); opacity: 1; }
  50% { transform: translateX(-50%) scaleY(1.05); opacity: 0.9; }
  100% { transform: translateX(-50%) scaleY(1); opacity: 1; }
}

@keyframes flickerCore {
  0% { transform: translateX(-50%) translateY(0) scale(1); }
  50% { transform: translateX(-50%) translateY(-2px) scale(1.05); }
  100% { transform: translateX(-50%) translateY(0) scale(1); }
}

.flame-out .flame-inner {
  transform: translateX(-50%) scale(0.3);
  opacity: 0.08;
  transition: transform 420ms ease, opacity 420ms ease;
}
.flame-out .flame-core {
  transform: translateX(-50%) scale(0.2);
  opacity: 0;
  transition: transform 420ms ease, opacity 420ms ease;
}

.greeting {
  margin-top: 8px;
  width: min(760px, 90vw);
  max-width: 760px;
  background: rgba(255,255,255,0.88);
  border-radius: 16px;
  padding: 18px 22px;
  box-shadow: 0 10px 30px rgba(99,58,70,0.12);
  transform: translateY(12px) scale(0.98);
  opacity: 0;
  text-align: center;
  transition: all 640ms cubic-bezier(.2,.9,.3,1);
  z-index: 12;
}

.greeting.visible {
  transform: translateY(0) scale(1);
  opacity: 1;
  transition-delay: 200ms;
}

.greeting h1 {
  margin: 8px 0;
  font-size: 28px;
  color: #ff4b9b;
  letter-spacing: -0.02em;
}

.greeting p {
  margin: 0;
  color: #6b4a56;
  font-size: 15px;
  line-height: 1.5;
}

.particles {
  position: absolute;
  bottom: 0;              /* start from bottom of greeting */
  left: 50%;              /* center horizontally */
  width: 0;               /* individual particles are absolutely positioned via left */
  height: 500px;          /* enough height for rising animation */
  overflow: visible;
  z-index: 15;            /* above greeting text */
  pointer-events: none;   /* don't block clicks */
}


.particle {
  position: absolute;
  bottom: 0;
  transform-origin: center;
  opacity: 0;
  animation-name: riseHeart;
  animation-timing-function: cubic-bezier(.2,.9,.3,1);
  animation-fill-mode: forwards;
}
@keyframes riseHeart {
  0%   { transform: translateY(0) scale(0.8); opacity: 0; }
  10%  { opacity: 1; }
  100% { transform: translateY(-420px) translateX(var(--driftX)) scale(1); opacity: 0; }
}





