  flex-direction: row;
  transform: rotate(-10deg);
}

.garland.top-right {
  top: 0;
  right: 0;
  flex-direction: row-reverse;
  transform: rotate(10deg);
}

/* Mobile: vertical along edges, pointing inward */
@media (max-width: 480px) {
  /* Container for vertical garlands */
  .garland.top-left,
  .garland.top-right {
    position: fixed;        /* stick to screen edges */
    top: 0;
    bottom: 0;
    width: 32px;            /* slightly larger for bigger flags */
    display: flex;
    flex-direction: column;
    justify-content: space-between; /* fill full height */
    align-items: center;
    z-index: 20;
    padding: 0;
  }

  /* Left garland on left edge */
  .garland.top-left {
    left: 10px;
    transform: rotate(0deg);
    
  }

  /* Right garland on right edge */
  .garland.top-right {
    right: 10px;
    transform: rotate(0deg);
  }

  /* Flags as triangles pointing inward */
  .garland .flag {
    width: 0;
    height: 0;
    border-left: 32px solid transparent;  /* larger triangle */
    border-right: 32px solid transparent;
    border-top: 40px solid red;           /* triangle color */
    transform-origin: center;
  }


  /* Rotate flags 180Â° so they point inward */
  .garland.top-left .flag {
    transform: rotate(-85deg);  /* points right */
  }

  .garland.top-right .flag {
    transform: rotate(85deg);    /* points left */
  }

  /* Increase number of flags to fill screen */
  .garland.top-left .flag,
  .garland.top-right .flag {
    flex: 1; /* each flag stretches equally to touch top/bottom edges */
  }
}

 









/* Letter Modal Overlay */
.letter-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0,0,0,0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 50;
  padding: 20px; /* small padding so box doesn't touch edges */
  overflow-y: auto; /* enable scrolling if content is taller */
  box-sizing: border-box;
}

/* Letter Box */
.letter-modal .letter-box {
  background-color: #fff8e7;
  padding: 40px 50px;
  border-radius: 20px;
  box-shadow: 0 8px 30px rgba(0,0,0,0.2);
  max-width: 600px;
  width: 100%;
  max-height: calc(100vh - 40px); /* prevent overflowing viewport */
  overflow-y: auto; /* scroll inside box if needed */
  text-align: center;
  position: relative;
}

