.balloon {
  position: absolute;
  bottom: -120px; /* start below screen */
  width: 60px;    /* bigger */
  height: 90px;   /* taller */
  border-radius: 50% 50% 50% 50%;
  background: radial-gradient(circle at 30% 30%, var(--color1), var(--color2));
  opacity: 0.95;
  animation: riseBalloon var(--duration) linear forwards;
  
}
.balloon::before {
  content: '';
  position: absolute;
  top: 20%;
  left: 25%;
  width: 18%;
  height: 25%;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 50%;
  filter: blur(2px);
  transform: rotate(20deg);
}


/* Balloon string */
.balloon::after {
  content: '';
  position: absolute;
  bottom: -40px;
  left: 50%;
  width: 2px;
  height: 40px;
  background: #888;
  transform: translateX(-50%);
}


/* Rise animation */
@keyframes riseBalloon {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 0;
  }
  10% { opacity: 1; }
  100% {
    transform: translateY(-140vh) rotate(var(--rotate, 15deg));
    opacity: 0.9;
  }
}
@keyframes sway {
  0%   { transform: translateX(0) rotate(0deg); }
  50%  { transform: translateX(-15px) rotate(-3deg); }
  100% { transform: translateX(15px) rotate(3deg); }
}







