/* force garlands above all other page layers */
.garland {
  position: fixed !important;   /* fixed to viewport */
  z-index: 99999 !important;    /* higher than your modals (which use ~120) */
  pointer-events: none;         /* so they don't block taps/clicks */
  top: 0;                       /* keep existing top/left/right rules */
  left: auto;
  right: auto;
}















/* Caption above the rose */
.rose-wrap {
  position: relative; /* anchor for caption */
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Caption container (positioned above the image) */
.rose-caption {
  position: absolute;
  top: 8%; /* push caption near the top of the rose area; adjust to taste */
  left: 50%;
  transform: translateX(-50%);
  z-index: 30; /* above the rose */
  text-align: center;
  pointer-events: none; /* allow clicks to pass through to modal close buttons */
  display: inline-flex;
  flex-direction: column;
  gap: 6px;
  align-items: center;
  backdrop-filter: blur(4px) saturate(1.05);
  -webkit-backdrop-filter: blur(4px) saturate(1.05);
  padding: 8px 14px;
  border-radius: 999px;
  background: linear-gradient(180deg, rgba(255,255,255,0.35), rgba(255,255,255,0.06));
  box-shadow: 0 6px 18px rgba(0,0,0,0.12);
  transform-origin: center top;
}

/* Two-line style: normal + highlighted word */
.rose-caption__line {
  display: block;
  font-family: "Parisienne", "Dancing Script", cursive;
  font-size: 29px;
  color: #fff7f2;
  text-shadow: 0 3px 12px rgba(0,0,0,0.45);
  letter-spacing: 0.2px;
  opacity: 0.95;
}

.rose-caption__highlight {
  display: block;
  font-family: "Poppins", system-ui, sans-serif;
  font-weight: 700;
  font-size: 22px;
  color: #ffd9a3; /* warm highlight */
  -webkit-text-stroke: 0.4px rgba(23,11,9,0.12);
  text-shadow: 0 6px 20px rgba(217,63,112,0.12);
  letter-spacing: 0.2px;
}

/* Gentle entrance animation */
@keyframes roseCaptionEnter {
  from { transform: translateX(-50%) translateY(-6px) scale(0.98); opacity: 0; }
  to   { transform: translateX(-50%) translateY(0) scale(1); opacity: 1; }
}

.rose-caption {
  animation: roseCaptionEnter 600ms cubic-bezier(.2,.9,.3,1) 180ms both;
}

/* Responsive scaling */
@media (max-width: 640px) {
  .rose-caption__line { font-size: 25px; }
  .rose-caption__highlight { font-size: 16px; }
  .rose-caption { top: 6%; padding: 6px 10px; }
}
@media (max-width: 420px) {
  .rose-caption__line {
    font-size: clamp(26px, 7vw, 30px); /* scales dynamically by screen width */
  }
}

@media (prefers-reduced-motion: reduce) {
  .rose-caption { animation: none; transform: translateX(-50%); }
}


